export default {
  data() {
    return {
      orderlist: ["ascending", "descending"],
      tableData: [
        {
          topuptype: "充值类型",
          maxamount: "自定义最大金额",
          minamount: "可自定义最小金额",
          paynode: "支付备注",
          effect: "是否生效",
          recommend: "是否推荐",
          operation: "操作者",
          operationtime: "操作时间"
        }
      ],
      rules: {},
      form: {
        sort: "",
        jianjie: "",
        aa: "文字",
        bb: "没有标签",
        paixun: "",
        zhuangtai: "展示",
        name: "按时间",
        channel: "",
        type: "支付宝",
        custom: "固定金额",
        common: "",
        remark: "0",
        maxcustom: "0",
        mincustom: "0",
        recommend: "不推荐",
        operant: "不生效"
      },

      currentPage4: 1,
      visiblity: false,
      title: "添加停服公告",
      selList:[],
    };
  },
  methods: {
    del() {
      //勾选需要删除的项目批量删除
      if (this.selList.length != 0) {
        //###1.删除dom的数据
        //2.删除后台的数据
        console.log("已经有数据了");
      } else {
        this.$message("请选择需要删除的数据");
      }
    },
    add() {
      this.visiblity = true;
      this.title = "添加停服公告";
    },

    handleSelectionChange(sel) {
      this.selList = sel
      console.log(sel);
      
    },
    handleSizeChange() {},
    handleCurrentChange() {},
    handleEdit() {
      this.visiblity = true;
      this.title = "更新停服公告";
    },
    handleDelete() {
      this.$confirm("确认删除吗?", "信息", {
        confirmButtonText: "确定",
        cancelButtonText: "取消"
      }).then(() => {
        this.$message({
          type: "success",
          message: "删除成功!"
        });
      });
    },
    onSubmit() {
      //表单验证
      this.$refs.form.validate(valid => {
        if (valid) {
          //1.将form数据传递到paylist,新增到table中先显示一个dom
          this.$store.commit("ADD_PAYITEM", this.form);
          //2.发送请求追加数据到后台-------------------------------------------------------------

          // console.log("发送请求");

          //3.关闭新增的弹框
          this.forminit();
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    back() {
      this.forminit();
    },
    forminit() {
      this.visiblity = false;
      //重置form表单的数据
    }
  }
};



